:root {
  --primary: #003E74;
  --blue: #1A85C7;
  --red: #f44336;
  --violet: #941276;
  --darkgrey: #aaaaaa;
  --medgrey: #d1d1d1;
  --lightgrey: #f7f7f7;
  --white: #fff;

  --xs: 4px;
  --s: 8px;
  --m: 16px;
  --l: 24px;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Open Sans", sans-serif;
  font-weight: 300;
  font-size: 18px;
}

h1,h2,h3,h4,h5,h6 {
  font-family: "Open Sans", sans-serif;
  margin-top: 0;
}

p {
  margin-top: 0;
}

form {
  -webkit-flex-grow: 1;
          flex-grow: 1;
}

a {
  color: gray;
}

a:hover {
  color: #003E74;
  color: var(--primary);
}

.App-container {
  margin: 0 0 40px 0;
}

.u-nomargin {
  margin: 0;
}

.u-nopadding {
  padding: 0;
}

.u-block{
  position: relative;
  width: 80%;
  padding: 0 10% 0% 10%;
  margin-top: 24px;
}

.u-flex {
  display: -webkit-flex;
  display: flex;
}

.u-flexColumn {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.u-flex-justifyCenter {
  -webkit-justify-content: center;
          justify-content: center;
}

.u-flex-justifySpace {
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.u-flex-alignCenter {
  -webkit-align-items: center;
          align-items: center;
}

.u-inlineBlock {
  display: inline-block;
}

.u-bold {
  font-weight: 600;
}

.u-textCenter {
  text-align: center;
}

.u-relative {
  position: relative;
}

.u-pointer {
  cursor: pointer;
}

.u-link {
  color: #003E74;
  color: var(--primary);
  text-decoration: none;
  cursor: pointer;
}

.u-link:hover {
  color: var(--primary--dim);
}

.u-ordinarybutton {
