@-webkit-keyframes aboutTitleAnimation {
    0% {
      letter-spacing: 0.7em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      letter-spacing: normal;
      opacity: 1;
    }
  }

@keyframes aboutTitleAnimation {
    0% {
      letter-spacing: 0.7em;
      opacity: 0;
    }
    40% {
      opacity: 0.6;
    }
    100% {
      letter-spacing: normal;
